@charset "UTF-8";
/*-----------------------------------------------------------------------------------*/
/* GENERAL STYLES
/*-----------------------------------------------------------------------------------*/
:root {
  --color-gris: #111;
  --color-gris-claro: #161616;
  --color-blanco: #fff;
  --color-rosa: #dc143f;
  --gris-item-entrada: #222222;
  --color-gris-metas: #adadad;
  --color-gris-texto: #adadad;
  --color-blanco-cat: #d6d6d6;
  --color-box-transparencia: linear-gradient(to top, black, transparent);
}

.claro-theme {
  --color-gris: #fff;
  --color-gris-claro: #161616;
  --color-blanco: #111;
  --color-rosa: #dc143f;
  --gris-item-entrada: #161616;
  --color-gris-metas: #130c0c;
  --color-gris-texto: #adadad;
  --color-blanco-cat: #222222;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0 !important;
  background-color: var(--color-gris);
  font-size: 18px;
  color: var(--color-blanco);
  font-family: "Roboto", sans-serif;
  font-size: 18px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Rajdhani", sans-serif !important;
  font-weight: 600;
  text-transform: uppercase;
}

.wrap {
  max-width: 1140px;
  width: 90%;
  margin: 0 auto;
}

.custom-logo {
  max-width: 130px;
  height: auto;
}

.btn-rosa {
  background-color: var(--color-rosa);
  color: var(--color-gris-color-blanco);
  text-decoration: none;
  padding: 10px;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
}
.btn-rosa:hover {
  background-color: var(--color-gris);
  color: var(--color-blanco);
}

.btn-rosa-cat {
  background-color: var(--color-rosa);
  color: var(-color-blanco) !important;
  text-decoration: none;
  padding: 0px 5px;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  color: var(--color-blanco);
}
.btn-rosa-cat:hover {
  background-color: var(--color-gris);
  color: var(--color-blanco) !important;
}

/*-----------------------------------------------------------------------------------*/
/* MOBILE STYLES
/*-----------------------------------------------------------------------------------*/
/*******************************************************************************/
/*********************IMPORTS COMPONENTS***********************/
/*******************************************************************************/
.wrap-hdr {
  padding: 5px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: solid 1px var(--color-blanco);
}
.wrap-hdr .nav-hdr {
  margin: 30px 0;
}
.wrap-hdr .logo .title {
  font-size: 40px;
  font-weight: bold;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  color: var(--color-blanco);
  text-decoration: none;
}
@media screen and (min-width: 769px) {
  .wrap-hdr {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
  }
  .wrap-hdr .nav-hdr {
    margin: 0;
  }
  .wrap-hdr .nav-hdr ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    gap: 20px;
  }
  .wrap-hdr .nav-hdr ul li a {
    text-decoration: none;
    color: var(--color-blanco);
    font-family: "Rajdhani", sans-serif;
    font-weight: 500;
    font-size: 15px;
    text-transform: uppercase;
    padding: 10px 20px;
    transition: color 0.3s ease;
  }
  .wrap-hdr .nav-hdr ul li a:hover {
    color: var(--color-rosa);
  }
}

.wrap-social {
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
}

.social {
  display: flex;
  list-style: none;
  padding: 0;
  flex-direction: row;
  gap: 10px;
}
.social li a {
  width: 30px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  border: solid 1px var(--color-blanco);
  border-radius: 50%;
  color: var(--color-blanco);
  text-decoration: none;
  font-size: 16px;
  transition: border-color 0.3s ease, color 0.3s ease;
}
.social li a:hover {
  color: var(--color-rosa);
  border-color: var(--color-rosa);
}

.icon-moon {
  color: #fff;
  padding: 10px;
  cursor: pointer;
}

.search-form {
  display: flex;
  align-items: center;
}
.search-form .search-field {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.search-form .search-submit {
  background-color: var(--color-rosa);
  color: white;
  border: none;
  padding: 8px 16px;
  margin-left: 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}
.search-form .search-submit:hover {
  background-color: var(--color-rosa);
}

.wrap-menu {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}
.wrap-menu .toggle {
  color: var(--color-blanco);
  cursor: pointer;
  font-size: 30px;
}
.wrap-menu .menu {
  background-color: var(--color-gris-claro);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease-in-out;
  z-index: 1;
}
.wrap-menu .menu .close {
  padding: 10px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-blanco);
  border: solid 1px var(--color-blanco);
  border-radius: 50%;
  cursor: pointer;
  margin: 30px;
}
.wrap-menu .menu nav {
  height: 100%;
  z-index: 10;
}
.wrap-menu .menu nav .menu-menu-1-container {
  height: 100%;
}
.wrap-menu .menu nav .menu-menu-1-container ul {
  list-style: none;
  padding: 0;
  height: 100%;
}
.wrap-menu .menu nav .menu-menu-1-container ul li {
  text-align: center;
  display: block;
}
.wrap-menu .menu nav .menu-menu-1-container ul li a {
  color: var(--color-blanco);
  padding: 10px 15px;
  display: block;
  text-decoration: none;
  font-family: "Rajdhani", sans-serif;
  font-size: 15px;
  text-transform: uppercase;
}
.wrap-menu .menu-active {
  opacity: 1;
  height: 100%;
  transition: overflow 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
@media screen and (min-width: 992px) {
  .wrap-menu .toggle {
    display: none;
  }
  .wrap-menu .menu {
    display: block;
    position: relative;
    background-color: transparent;
    opacity: 1;
    height: auto;
  }
  .wrap-menu .menu .close {
    display: none;
  }
  .wrap-menu .menu nav {
    height: 100%;
  }
  .wrap-menu .menu nav .menu-menu-1-container {
    height: 100%;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul {
    display: flex;
    justify-content: space-evenly;
    margin: 0;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul li a {
    color: var(--color-blanco);
    padding: 10px;
    display: block;
    font-weight: 500;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul li a:hover {
    color: var(--color-rosa);
  }
}

.comp-footer {
  display: grid;
  gap: 20px;
}
.comp-footer .item-wrap {
  background-color: var(--color-gris-claro);
  padding: 30px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.comp-footer .item-wrap .logo .title {
  font-size: 40px;
  font-weight: bold;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  color: var(--color-blanco);
  text-decoration: none;
  text-align: center;
  display: block;
}
.comp-footer .item-wrap p {
  max-width: 600px;
  text-align: center;
}

.grid-principal {
  display: grid;
  gap: 20px;
  margin-bottom: 80px;
}
.grid-principal .item-grid-prin {
  height: 250px;
  overflow: hidden;
  position: relative;
}
.grid-principal .item-grid-prin .item-hov-zoom {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}
.grid-principal .item-grid-prin .container-data {
  position: absolute;
  bottom: 0;
  left: 0;
  background: var(--color-box-transparencia);
  padding: 20px;
  width: 100%;
}
.grid-principal .item-grid-prin .container-data h2 {
  margin: 0;
  font-size: 20px;
  color: var(--color-blanco);
  font-weight: bold;
  transition: color 0.3s ease;
  text-shadow: 1px 1px 6px black;
}
.grid-principal .item-grid-prin .container-data h2:hover {
  color: var(--color-rosa);
}
.grid-principal .item-grid-prin .container-data ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: var(--color-blanco);
}
.grid-principal .item-grid-prin .container-data ul li {
  font-size: 16px;
  color: var(--color-gris-metas);
}
.grid-principal .item-grid-prin .container-data ul li span {
  color: var(--color-blanco);
}
.grid-principal .item-grid-prin:hover > .item-hov-zoom {
  transform: scale(1.1);
}
@media screen and (min-width: 600px) {
  .grid-principal {
    grid-template-columns: 1fr 1fr;
  }
  .grid-principal .item-grid-prin {
    height: 350px;
  }
  .grid-principal .item-grid-prin .container-data span {
    font-size: 15px;
  }
  .grid-principal .item-grid-prin .container-data h2 {
    font-size: 28px;
  }
  .grid-principal .item-grid-prin .container-data ul li {
    font-size: 15px;
  }
  .grid-principal .item-grid-prin:nth-of-type(1) {
    height: 550px;
    grid-column: 1/3;
  }
}

.segunda-cat {
  margin-bottom: 80px;
}
.segunda-cat .grid-item-entada {
  list-style: none;
  padding: 0;
  display: grid;
  gap: 20px;
}
.segunda-cat .grid-item-entada li {
  overflow: hidden;
}
.segunda-cat .grid-item-entada li img {
  height: 250px;
  width: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}
.segunda-cat .grid-item-entada li img:hover {
  transform: scale(1.1);
}
.segunda-cat .grid-item-entada li div {
  margin-top: -10px;
  padding: 20px;
  height: 170px;
  background: var(--gris-item-entrada);
  z-index: 1;
  position: relative;
}
.segunda-cat .grid-item-entada li div span {
  padding: 0;
}
.segunda-cat .grid-item-entada li div ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: var(--color-blanco);
}
.segunda-cat .grid-item-entada li div ul li {
  font-size: 16px;
  color: var(--color-gris-metas);
}
.segunda-cat .grid-item-entada li div ul li span {
  color: var(--color-blanco);
  font-size: 16px;
}
.segunda-cat .grid-item-entada li div a {
  color: var(--color-blanco);
  text-decoration: none;
  transition: color 0.3s ease;
}
.segunda-cat .grid-item-entada li div a h3 {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: bold;
  text-shadow: 1px 1px 1px black;
}
.segunda-cat .grid-item-entada li div a:hover {
  color: var(--color-rosa);
}
@media screen and (min-width: 769px) {
  .segunda-cat .grid-item-entada {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 992px) {
  .segunda-cat .grid-item-entada {
    grid-template-columns: repeat(3, 1fr);
  }
}

.component-hov-box {
  margin-bottom: 80px;
}
.component-hov-box .item-component-hov-box {
  display: grid;
  gap: 20px;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box {
  height: 250px;
  overflow: hidden;
  position: relative;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .a-item-component-hov-box {
  height: 250px;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .a-item-component-hov-box .item-hov-zoom {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .a-item-component-hov-box:hover > .item-hov-zoom {
  transform: scale(1.1);
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data {
  background: var(--color-box-transparencia);
  padding: 10px 20px;
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data a {
  text-decoration: none;
  margin: 0;
  color: var(--color-blanco);
  transition: color 0.3s ease;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data a h3 {
  text-decoration: none;
  margin: 0;
  margin-top: 5px;
  font-weight: bold;
  text-shadow: 1px 1px 6px black;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data a:hover {
  color: var(--color-rosa);
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data ul li {
  font-size: 16px;
  color: var(--color-gris-metas);
}
.component-hov-box .item-component-hov-box .div-item-component-hov-box .container-data ul li span {
  color: var(--color-blanco);
  font-size: 16px;
}
@media screen and (min-width: 600px) {
  .component-hov-box .item-component-hov-box {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (min-width: 769px) {
  .component-hov-box .item-component-hov-box {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 992px) {
  .component-hov-box .item-component-hov-box {
    grid-template-columns: repeat(3, 1fr);
  }
}

.hero-singlepost img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}
.hero-singlepost .data-title {
  margin-top: 20px;
}
.hero-singlepost .data-title div:nth-of-type(1) {
  text-align: center;
}
.hero-singlepost .data-title div:nth-of-type(1) a {
  font-size: 16px;
  padding: 5px 10px;
  background-color: var(--color-rosa);
  margin-bottom: 5px;
  display: inline-block;
  text-decoration: none;
  color: var(--color-blanco);
}
.hero-singlepost .data-title div:nth-of-type(1) h1 {
  font-size: 28px;
  text-align: center;
  margin: 10px 0;
  color: var(--color-blanco-cat);
}
.hero-singlepost .data-title div:nth-of-type(2) {
  text-align: center;
  color: var(--color-gris-metas);
}
.hero-singlepost .data-title div:nth-of-type(2) span {
  color: var(--color-blanco-cat);
}
@media screen and (min-width: 769px) {
  .hero-singlepost {
    height: 400px;
    overflow: hidden;
    position: relative;
    margin-bottom: 80px;
  }
  .hero-singlepost img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .hero-singlepost .data-title {
    background: var(--color-box-transparencia);
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: end;
    padding: 10px 20px;
    width: 100%;
    height: 200px;
  }
  .hero-singlepost .data-title div:nth-of-type(1) h1 {
    margin: 0;
  }
}
@media screen and (min-width: 992px) {
  .hero-singlepost {
    height: 600px;
  }
  .hero-singlepost .data-title div:nth-of-type(1) h1 {
    font-size: 35px;
  }
}

.contein-single-post {
  display: grid;
  gap: 30px;
  margin-bottom: 80px;
}
.contein-single-post .sidebar-archive ul {
  padding: 0;
}
.contein-single-post .sidebar-archive ul .wp-block-latest-posts__list,
.contein-single-post .sidebar-archive ul .wp-block-archives-list,
.contein-single-post .sidebar-archive ul .wp-block-categories-list {
  padding-left: 10px;
}
.contein-single-post p {
  color: var(--color-gris-texto);
}
.contein-single-post .wp-block-list li {
  color: var(--color-blanco-cat);
  margin-bottom: 15px;
}
.contein-single-post h2,
.contein-single-post h3,
.contein-single-post h4,
.contein-single-post h5,
.contein-single-post h6 {
  color: var(--color-blanco-cat);
}
.contein-single-post .wp-block-latest-posts__list,
.contein-single-post .wp-block-archives-list,
.contein-single-post .wp-block-categories-list {
  list-style: none;
  padding-left: 10px;
}
.contein-single-post .wp-block-latest-posts__list li,
.contein-single-post .wp-block-archives-list li,
.contein-single-post .wp-block-categories-list li {
  position: relative;
  padding-left: 20px;
}
.contein-single-post .wp-block-latest-posts__list li ::before,
.contein-single-post .wp-block-archives-list li ::before,
.contein-single-post .wp-block-categories-list li ::before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  background-image: url("./../../src/icons/chrevron_ritgth.png");
  background-size: contain;
  margin-right: 5px;
  position: absolute;
  left: 0px;
  top: 5px;
}
.contein-single-post .wp-block-latest-posts__list li a,
.contein-single-post .wp-block-archives-list li a,
.contein-single-post .wp-block-categories-list li a {
  color: var(--color-blanco-cat);
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease;
}
.contein-single-post .wp-block-latest-posts__list li a:hover,
.contein-single-post .wp-block-archives-list li a:hover,
.contein-single-post .wp-block-categories-list li a:hover {
  color: var(--color-rosa);
}
@media screen and (min-width: 769px) {
  .contein-single-post {
    grid-template-columns: 3fr 1fr;
  }
}

.arr-single-post {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 30px 0;
}
.arr-single-post .post-navigation__next a,
.arr-single-post .post-navigation__prev a {
  text-decoration: none;
  color: var(--color-blanco);
  background-color: var(--color-rosa);
  padding: 10px 20px;
}
.arr-single-post .post-navigation__next a .arr-wrod,
.arr-single-post .post-navigation__prev a .arr-wrod {
  display: none;
}
@media screen and (min-width: 769px) {
  .arr-single-post .post-navigation__next a .arr-wrod,
  .arr-single-post .post-navigation__prev a .arr-wrod {
    display: contents;
  }
}
@media screen and (min-width: 769px) {
  .arr-single-post {
    margin: 80px 0;
  }
}

.content-post hr {
  margin-top: 80px;
}

.related-posts ul {
  padding: 0;
  list-style: none;
  display: grid;
  gap: 20px;
}
.related-posts ul li a {
  color: var(--color-gris-texto);
  text-decoration: none;
  transition: color 0.3s ease;
}
.related-posts ul li a:hover {
  color: var(--color-rosa);
}
.related-posts ul li a img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}
@media screen and (min-width: 769px) {
  .related-posts ul {
    grid-template-columns: repeat(2, 1fr);
  }
  .related-posts ul li a img {
    height: 150px;
  }
}
@media screen and (min-width: 992px) {
  .related-posts ul {
    grid-template-columns: repeat(3, 1fr);
  }
}

.nav-links .page-numbers {
  background-color: var(--color-rosa);
  color: var(--color-blanco);
  padding: 10px;
  text-decoration: none;
}
.nav-links .page-numbers .page-numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.nav-links .page-numbers img {
  width: 40px;
}
.nav-links .nav-previous a {
  background-color: var(--color-rosa);
  color: var(--color-blanco);
  padding: 10px;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}

.navigation {
  margin-top: 80px;
}
.navigation h2.screen-reader-text {
  display: none;
}

.shared {
  margin-bottom: 50px;
}
.shared ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2px;
  margin-top: 0;
}
.shared ul .icon-shared {
  border-radius: 2px;
}
.shared ul .icon-shared a {
  color: #fff;
  padding: 30px 15px;
  font-size: 25px;
}
.shared ul .whatsapp {
  background-color: #25D366;
}
.shared ul .telegram {
  background-color: #0088CC;
}
.shared ul .linkendin {
  background-color: #0077B5;
}
.shared ul .facebook {
  background-color: #1877F2;
}
.shared ul .twitter {
  background-color: #000000;
